.catalog {
  padding: 20px 0 50px;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 20px;
  }
  &__content {
    position: relative;
    z-index: 2;
    width: calc(100% - 350px);
  }
  &__top {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    gap: 8px 16px;
  }
  &__group {
    display: flex;
    align-items: stretch;
    gap: 16px;
  }
  &__selects {
    display: flex;
    align-items: stretch;
    gap: 16px;
  }
  .sorting-select {
    .nice-select {
      min-width: 195px;
      padding-left: 36px;
      &:before {
        content: '';
        position: absolute;
        top: 50%;
        left: 15px;
        transform: translate(0, -50%);
        width: 15px;
        height: 15px;
        background-image: url("../img/sorting-icon.svg");
        background-repeat: no-repeat;
        background-position: center;
        background-size: auto;
      }
    }
  }
  .nice-select {
    z-index: 5;
    width: auto;
    min-width: 96px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid #ECECEC;
    border-radius: 12px;
    padding: 5px 30px 5px 15px;
    font-size: 14px;
    line-height: 130%;
    white-space: normal;
    &:after {
      top: 50%;
      right: 15px;
      transform: translate(0, -50%);
      width: 11px;
      height: 6px;
      background-image: url("../img/down-arrow.svg");
      background-repeat: no-repeat;
      background-position: center;
      background-size: auto;
    }
    &:hover {
      &:after {
        background-image: url("../img/down-arrow3.svg");
      }
    }
    .current {
      white-space: nowrap;
      overflow: hidden;
    }
    .list {
      z-index: 3;
      width: 100%;
      min-width: 50px;
      background: #fff;
      box-shadow: 0px 15px 35.5px 0px rgba(118, 118, 118, 0.25);
      border-radius: 12px;
      padding: 0 5px;
      li {
        background-color: transparent !important;
        border: none;
        padding: 6px 0;
        font-size: 14px;
        line-height: 125%;
        color: #9F9F9F;
        &:first-child {
          padding-top: 8px;
        }
        &:last-child {
          padding-bottom: 8px;
        }
        &:not(:last-child) {
          border-bottom: 1px solid #ECECEC;
        }
      }
      li:hover {
        color: #3F3F3F;
      }
      .selected {
        font-weight: 500;
        color: #3F3F3F;
      }
    }
  }
  .open {
    &:after {
      transform: translate(0, -50%) rotate(-180deg);
      background-image: url("../img/down-arrow3.svg");
    }
  }
  .filter-btn {
    height: 40px;
    display: none;
    justify-content: center;
    align-items: center;
    gap: 8px;
    border: 1px solid #ECECEC;
    border-radius: 12px;
    padding: 5px 15px;
    cursor: pointer;
    i {
      width: 15px;
      img {
        width: 100%;
      }
    }
    span {
      font-size: 14px;
      line-height: 130%;
    }
  }
  &__themes {
    margin-top: 20px;
    @media screen and (min-width: 577px) {
      .swiper-wrapper {
        transform: unset;
        display: flex;
        flex-wrap: wrap;
        gap: 15px;
      }
      .swiper-slide {
        margin: 0 !important;
      }
    }
  }
  &__theme {
    width: auto !important;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
    background: #F3F3FF;
    border-radius: 16px;
    padding: 5px 15px;
    span {
      font-size: 14px;
      line-height: 130%;
      color: #5856D2;
    }
    i {
      width: 23px;
      flex-shrink: 0;
      cursor: pointer;
      img {
        width: 100%;
      }
    }
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 48px / 4));
    gap: 20px 16px;
    margin-top: 20px;
  }
  .work-card {
    &__title {
      min-height: 41px;
      font-size: 16px;
    }
    &__price-news {
      font-size: 18px;
    }
    &__price-old {
      font-size: 12px;
    }
    &__discount {
      font-size: 12px;
    }
  }
  .pagination {
    margin-top: 30px;
  }
}
