.catalog-sidebar {
  width: 330px;
  &__group {
    border-bottom: 1px solid #ECECEC;
    padding: 20px 0;
    &:first-child {
      padding-top: 0;
    }
    &:last-child {
      border-bottom: unset;
      padding-bottom: 0;
    }
  }
  &__item {
    &-show {
      display: flex;
      justify-content: space-between;
      align-items: center;
      gap: 8px;
      cursor: pointer;
    }
    .item-show--active {
      .catalog-sidebar__item-btn {
        &:before {
          display: none;
        }
      }
    }
    &-title {
      font-size: 18px;
      line-height: 130%;
      font-weight: 700;
    }
    &-btn {
      position: relative;
      width: 16px;
      height: 16px;
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 100%;
        height: 1.5px;
        background: #5856D2;
      }
      &:before {
        content: '';
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 1.5px;
        height: 100%;
        background: #5856D2;
      }
    }
    &-hidden {
      display: none;
      margin-top: 18px;
    }
    .item-hidden--active {
      display: block;
    }
    .form-group {
      margin-bottom: 18px;
      input {
        width: 100%;
        height: 45px;
        border: 1px solid #ECECEC;
        padding-left: 20px;
        font-size: 14px;
      }
    }
  }
  &__checkboxes {
    display: grid;
    grid-template-columns: 100%;
    gap: 15px;
  }
  &__checkbox {
    position: relative;
    &:nth-child(7) ~ .catalog-sidebar__checkbox {
      display: none;
    }
    input {
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      padding: 0;
      margin: 0;
      cursor: pointer;
      opacity: 0;
    }
    input:checked ~ label {
      &:after {
        background-image: url("../img/check.svg");
        background-color: #5856D2;
        border-color: #5856D2;
      }
    }
    label {
      position: relative;
      display: block;
      padding-left: 38px;
      font-size: 16px;
      line-height: 130%;
      letter-spacing: 0.32px;
      &:after {
        content: '';
        position: absolute;
        top: -2.5px;
        left: 0;
        width: 23px;
        height: 23px;
        background-repeat: no-repeat;
        background-position: center;
        background-size: auto;
        -webkit-background-size: auto;
        -moz-background-size: auto;
        -o-background-size: auto;
        border: 1.5px solid #ECECEC;
        border-radius: 5px;
      }
    }
  }
  &__more {
    font-size: 14px;
    line-height: 130%;
    color: #9F9F9F;
    margin-top: 18px;
    cursor: pointer;
    span {
      text-decoration: underline;
    }
    span:nth-child(2) {
      display: none;
    }
  }
  .show-more--active {
    span:nth-child(1) {
      display: none;
    }
    span:nth-child(2) {
      display: block;
    }
  }
  .text-overflow--active {
    .catalog-sidebar__checkbox {
      display: block !important;
    }
  }
  &__price-inputs {
    position: relative;
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 27px / 2));
    gap: 27px;
    margin-bottom: 18px;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 7px;
      height: 1.5px;
      background: #9F9F9F;
      border-radius: 5px;
      pointer-events: none;
    }
    .form-group {
      margin-bottom: 0;
      input::-webkit-outer-spin-button,
      input::-webkit-inner-spin-button {
        -webkit-appearance: none;
      }
    }
  }
  &__btns {
    width: 100%;
  }
  &__btn {
    width: 100%;
    height: 52px;
    span {
      font-size: 16px;
    }
  }
  .clear-btn {
    display: none;
  }
  &__top {
    display: none;
  }
}
